import { useState, useEffect, useCallback, useRef } from 'react';
import { AgentStatus, ConnectionState, isValidAgentStatus, createDefaultConnectionState, DEFAULT_POLL_INTERVAL } from '../types';

// Default R2 URL - can be overridden via environment variable
const R2_URL = 'https://example.r2.cloudflarestorage.com/status.json';

export function useStatusPolling() {
  const [status, setStatus] = useState<AgentStatus | null>(null);
  const [connectionState, setConnectionState] = useState<ConnectionState>(createDefaultConnectionState);
  const [error, setError] = useState<string | null>(null);
  const intervalRef = useRef<number | null>(null);

  const fetchStatus = useCallback(async () => {
    try {
      const response = await fetch(R2_URL);
      
      if (!response.ok) {
        throw new Error(`HTTP ${response.status}`);
      }

      const data = await response.json();

      if (!isValidAgentStatus(data)) {
        throw new Error('Invalid status format');
      }

      setStatus(data);
      setConnectionState(prev => ({
        ...prev,
        connected: true,
        lastSuccessTime: Date.now(),
        failureCount: 0,
      }));
      setError(null);
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Unknown error';
      setError(errorMessage);
      setConnectionState(prev => ({
        ...prev,
        connected: false,
        failureCount: prev.failureCount + 1,
      }));
    }
  }, []);

  useEffect(() => {
    // Initial fetch
    fetchStatus();

    // Set up polling
    intervalRef.current = window.setInterval(fetchStatus, DEFAULT_POLL_INTERVAL);

    return () => {
      if (intervalRef.current !== null) {
        clearInterval(intervalRef.current);
      }
    };
  }, [fetchStatus]);

  return { status, connectionState, error };
}