import { useEffect, useState } from 'react';
import ReactP5 from 'react-p5';
import { AgentStatus, ConnectionState, HeartbeatState, CANVAS_WIDTH, CANVAS_HEIGHT } from '../types';

interface HeartbeatCanvasProps {
  status: AgentStatus | null;
  connectionState: ConnectionState;
}

export function HeartbeatCanvas({ status, connectionState }: HeartbeatCanvasProps) {
  const [sketchState, setSketchState] = useState<HeartbeatState>('disconnected');

  useEffect(() => {
    if (!connectionState.connected) {
      setSketchState('disconnected');
    } else if (status?.busy) {
      setSketchState('busy');
    } else {
      setSketchState('idle');
    }
  }, [status, connectionState]);

  const setup = (p5: any, canvasParentRef: Element) => {
    p5.createCanvas(CANVAS_WIDTH, CANVAS_HEIGHT).parent(canvasParentRef);
  };

  const draw = (p5: any) => {
    p5.background(245);

    const centerX = p5.width / 2;
    const centerY = p5.height / 2;
    const amplitude = 30;
    const frequency = 0.02;
    const speed = 0.05;

    // Set colors based on state
    let color: number[];
    switch (sketchState) {
      case 'idle':
        color = [76, 175, 80]; // Green
        break;
      case 'busy':
        color = [244, 67, 54]; // Red
        break;
      case 'disconnected':
      default:
        color = [158, 158, 158]; // Gray
        break;
    }

    // Draw wave
    p5.stroke(color[0], color[1], color[2]);
    p5.strokeWeight(3);
    p5.noFill();

    p5.beginShape();
    for (let x = 0; x < p5.width; x += 2) {
      let y = centerY;
      
      if (sketchState === 'busy') {
        // Pulsing effect for busy state
        const pulse = Math.sin(p5.millis() * 0.003) * 0.3 + 1;
        y = centerY + Math.sin(x * frequency + p5.frameCount * speed) * amplitude * pulse;
      } else {
        // Normal sine wave
        y = centerY + Math.sin(x * frequency + p5.frameCount * speed) * amplitude;
      }
      
      p5.vertex(x, y);
    }
    p5.endShape();

    // Draw center indicator
    p5.noStroke();
    p5.fill(color[0], color[1], color[2], 100);
    p5.circle(centerX, centerY, 20);
    p5.fill(color[0], color[1], color[2]);
    p5.circle(centerX, centerY, 10);
  };

  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
      <ReactP5 setup={setup} draw={draw} />
    </div>
  );
}