/**
 * Face Frontend - Type Definitions
 * 
 * Core types for OpenClaw Face status monitoring dashboard.
 */

/**
 * Agent status received from R2 storage
 */
export interface AgentStatus {
  /** Whether the agent is currently busy processing a task */
  busy: boolean;
  /** The name of the model currently being used */
  model: string;
  /** Unix timestamp in milliseconds */
  ts: number;
  /** Optional task identifier */
  taskId?: string;
}

/**
 * Connection state for polling status
 */
export interface ConnectionState {
  /** Whether the connection is currently successful */
  connected: boolean;
  /** Timestamp of the last successful connection (Unix ms) */
  lastSuccessTime: number;
  /** Number of consecutive failures */
  failureCount: number;
}

/**
 * Dashboard configuration
 */
export interface DashboardConfig {
  /** R2 public URL for status.json */
  r2Url: string;
  /** Polling interval in milliseconds */
  pollInterval: number;
  /** Maximum consecutive failures before showing error */
  maxFailures: number;
}

/**
 * Heartbeat animation states
 */
export type HeartbeatState = 'idle' | 'busy' | 'disconnected';

/**
 * Heartbeat sketch interface for p5.js
 */
export interface HeartbeatSketch {
  /** p5.js setup function */
  setup: (p5: P5, canvasParentRef: HTMLElement) => void;
  /** p5.js draw function */
  draw: (p5: P5) => void;
  /** Update the current status */
  updateStatus: (status: AgentStatus | null) => void;
  /** Update the connection state */
  updateConnection: (connected: boolean) => void;
  /** Get current animation state */
  getState: () => HeartbeatState;
}

/**
 * P5.js type definition (simplified for TypeScript)
 */
declare global {
  interface P5 {
    createCanvas: (w: number, h: number, type?: string) => P5;
    background: (v1: number, v2?: number, v3?: number, v4?: number) => void;
    fill: (v1: number, v2?: number, v3?: number, v4?: number) => void;
    noFill: () => void;
    stroke: (v1: number, v2?: number, v3?: number, v4?: number) => void;
    noStroke: () => void;
    strokeWeight: (weight: number) => void;
    line: (x1: number, y1: number, x2: number, y2: number) => void;
    ellipse: (x: number, y: number, w: number, h?: number) => void;
    circle: (x: number, y: number, d: number) => void;
    rect: (x: number, y: number, w: number, h: number, tl?: number, tr?: number, br?: number, bl?: number) => void;
    text: (str: string, x: number, y: number) => void;
    textSize: (size: number) => void;
    textAlign: (alignX: number, alignY?: number) => void;
    sin: (angle: number) => number;
    cos: (angle: number) => number;
    map: (value: number, start1: number, stop1: number, start2: number, stop2: number) => number;
    millis: () => number;
    frameCount: number;
    width: number;
    height: number;
  }
}

/**
 * Default polling interval (5 seconds)
 */
export const DEFAULT_POLL_INTERVAL = 5000;

/**
 * Default maximum failures before showing error
 */
export const DEFAULT_MAX_FAILURES = 3;

/**
 * Canvas dimensions
 */
export const CANVAS_WIDTH = 400;
export const CANVAS_HEIGHT = 200;

/**
 * Animation transition duration (1 second)
 */
export const TRANSITION_DURATION = 1000;

/**
 * Validates an AgentStatus object from JSON
 * @param data - The data to validate
 * @returns true if the data is a valid AgentStatus
 */
export function isValidAgentStatus(data: unknown): data is AgentStatus {
  if (typeof data !== 'object' || data === null) {
    return false;
  }

  const d = data as Record<string, unknown>;

  if (typeof d.busy !== 'boolean') {
    return false;
  }

  if (typeof d.model !== 'string' || d.model.length === 0 || d.model.length > 100) {
    return false;
  }

  if (typeof d.ts !== 'number' || !Number.isInteger(d.ts) || d.ts <= 0) {
    return false;
  }

  if (d.taskId !== undefined && (typeof d.taskId !== 'string' || d.taskId.length === 0)) {
    return false;
  }

  return true;
}

/**
 * Validates a ConnectionState object
 * @param state - The state to validate
 * @returns true if the state is valid
 */
export function isValidConnectionState(state: unknown): state is ConnectionState {
  if (typeof state !== 'object' || state === null) {
    return false;
  }

  const s = state as Record<string, unknown>;

  if (typeof s.connected !== 'boolean') {
    return false;
  }

  if (typeof s.lastSuccessTime !== 'number' || !Number.isInteger(s.lastSuccessTime) || s.lastSuccessTime < 0) {
    return false;
  }

  if (typeof s.failureCount !== 'number' || !Number.isInteger(s.failureCount) || s.failureCount < 0) {
    return false;
  }

  return true;
}

/**
 * Creates a default ConnectionState
 * @returns A new ConnectionState with default values
 */
export function createDefaultConnectionState(): ConnectionState {
  return {
    connected: false,
    lastSuccessTime: 0,
    failureCount: 0,
  };
}

/**
 * Formats a timestamp for display
 * @param ts - Unix timestamp in milliseconds
 * @returns Formatted time string (HH:MM:SS)
 */
export function formatTimestamp(ts: number): string {
  const date = new Date(ts);
  return date.toLocaleTimeString('en-US', {
    hour12: false,
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit',
  });
}