import { Box, Alert, Chip } from '@mui/material';

interface ConnectionIndicatorProps {
  connected: boolean;
  failureCount: number;
  error: string | null;
}

export function ConnectionIndicator({ connected, failureCount, error }: ConnectionIndicatorProps) {
  if (connected) {
    return (
      <Box sx={{ textAlign: 'center' }}>
        <Chip
          label="Connected"
          color="success"
          size="small"
        />
      </Box>
    );
  }

  return (
    <Box sx={{ textAlign: 'center' }}>
      {failureCount >= 3 ? (
        <Alert severity="error">
          Connection failed: {error || 'Unknown error'}
        </Alert>
      ) : (
        <Chip
          label="Reconnecting..."
          color="warning"
          size="small"
        />
      )}
    </Box>
  );
}